import otherLogin from '@/common/common/other-login.vue';
export default {
	components:{
		otherLogin
	},
	data() {
		return {
			islogin: true,
			providerList:this.$store.state.providerList,
			homeList:[
				{
					text:'',
					menu:'浏览历史'
				}
			]
		};
	},
	onNavigationBarButtonTap(e) {
		if (e.index == 0) {
			uni.navigateTo({
				url:'../setting/setting'
			})
		}
	},
	methods: {
		toLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		toUserSpace(){
			uni.navigateTo({
				url:'../user-space/user-space'
			})
		}
	}
};
